import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import styled from 'styled-components';
import AlertLabel from '../../components/AlertLabel';
import Button from '../../components/Button';
import GridContainer from '../../components/GridContainer';
import PageHeader from '../../components/PageHeader';
import SafeHistory from '../../components/SafeHistory';
import StartShutdown from '../../components/StartShutdown';
import { useStoreActions, useStoreState } from '../../store';

const EmergencyShutdown = () => {
  const { t } = useTranslation();
  const [showWarning, setShowWarning] = useState(false);
  const {
    popupsModel: popupsActions,
    safeModel: safeActions,
  } = useStoreActions((state) => state);
  const { safeModel: safeState } = useStoreState((state) => state);

  const handleRedeemETH = () => {
    safeActions.setOperation(0);
    popupsActions.setESMOperationPayload({ isOpen: true, type: 'ETH' });
  };

  const handleRedeemRAI = () => {
    if (Number(safeState.totalEth) > 0) {
      setShowWarning(true);
    } else {
      safeActions.setOperation(0);
      popupsActions.setESMOperationPayload({ isOpen: true, type: 'RAI' });
    }
  };

  useEffect(() => {
    if (showWarning) {
      setTimeout(() => setShowWarning(false), 3000);
    }
  }, [showWarning]);

  return (
    <>
      <GridContainer>
        <PageHeader title={t('emergency_shutdown')} text={t('esm_text')} />
        {showWarning ? (
          <AlertContainer>
            <AlertLabel
              text={
                'To redeem your RAI for ETH you first need to withdraw your ETH'
              }
              type="warning"
            />
          </AlertContainer>
        ) : null}
        <StartShutdown />
        <ClaimBlocks>
          <ClaimItem>
            <InnerContent>
              <Value>{safeState.totalEth} ETH</Value>
              <Label>{'Claimable ETH from Safes'}</Label>

              <Actions>
                <Button
                  withArrow={Number(safeState.totalEth) !== 0}
                  text={t('redeem')}
                  onClick={handleRedeemETH}
                  disabled={Number(safeState.totalEth) === 0}
                  dimmed={Number(safeState.totalEth) === 0}
                />
              </Actions>
            </InnerContent>
          </ClaimItem>

          <ClaimItem>
            <InnerContent>
              <Value>{safeState.totalRAI} ETH</Value>
              <Label>{'Claimable RAI'}</Label>
              <Actions>
                <Button
                  withArrow
                  text={t('redeem')}
                  onClick={handleRedeemRAI}
                  disabled={Number(safeState.totalRAI) === 0}
                />
              </Actions>
            </InnerContent>
          </ClaimItem>
        </ClaimBlocks>
        <SafeHistory />
      </GridContainer>
    </>
  );
};

export default EmergencyShutdown;

const ClaimBlocks = styled.div`
  display: flex;
  margin: 0 -7.5px;
  ${({ theme }) => theme.mediaWidth.upToSmall`
    flex-wrap:wrap;
  `}
`;

const ClaimItem = styled.div`
  flex: 0 0 50%;
  padding: 0 7.5px;
  ${({ theme }) => theme.mediaWidth.upToSmall`
    flex: 0 0 100%;
  `}
`;

const InnerContent = styled.div`
  padding: 20px 20px 15px;
  border: 1px solid ${(props) => props.theme.colors.border};
  border-radius: ${(props) => props.theme.global.borderRadius};
  background: ${(props) => props.theme.colors.background};
  margin-bottom: 15px;
  text-align: center;
`;

const Value = styled.div`
  color: ${(props) => props.theme.colors.primary};
  font-size: ${(props) => props.theme.font.large};
  line-height: 27px;
  letter-spacing: -0.69px;
  font-weight: 600;
  ${({ theme }) => theme.mediaWidth.upToSmall`
  font-size: ${(props) => props.theme.font.medium};
`}
`;

const Label = styled.div`
  color: ${(props) => props.theme.colors.secondary};
  font-size: ${(props) => props.theme.font.small};
  line-height: 21px;
  letter-spacing: -0.09px;
  margin-top: 8px;
  ${({ theme }) => theme.mediaWidth.upToSmall`
    font-size: ${(props) => props.theme.font.extraSmall};
  `}
`;

const Actions = styled.div`
  display: flex;
  margin-top: 1rem;
  justify-content: flex-end;
`;

const AlertContainer = styled.div`
  margin-bottom: 15px;
`;
